export default {
  data() {
    return {
      // 邀请码输入框的内容
      invitationVal: ""
    };
  },
  methods: {
		// 点击确定按钮
		goIndex() {
			uni.switchTab({
				url: "/pages/index/index"
			})
		}
  },
  computed: {
    isActive() {
      if (this.invitationVal === '') {
        return false
      }
      return true
    }
  },
};
